<template>
  <div id="step4">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>

    <ul class="step-list">
      <li :class="item.index==activeClass[index]?'active':''" v-for="(item,index) in stepList">
        <img :src="getUrl(item.index)" alt="" v-if="activeClass[index]==item.index">
        <img :src="getUrl('2'+item.index)" alt="" v-if="activeClass[index]!=item.index">
        <span>{{item.txt}}</span>
      </li>
    </ul>
    <p class="succ-txt">3~5</p>
    <div class="tips-txt">
      
    </div>
    <router-link to="/" class="closeBtn"></router-link>
  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "step4",
    data(){
      return{
        imgUrl:"",
        activeClass:[],
        imgBaseUrl:"../../assets/img/seminar/",
        stepList:this.$store.state.stepList,
        showTitle:'',
      }
    },
    created(){
    },
    mounted(){
      this.activeClass=this.$store.state.stepClass;
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$store.commit("stepGo",3);
        this.$refs.child.backUp();
      },
      //emit
      getImgUrl(data) {
        //data  
        console.log(data)
      },
      //
      getUrl(icon){
        return require("@/assets/img/seminar/"+icon+".png");
      },
    }
  }
</script>

<style scoped lang="less">
  .succ-txt{
    font-size: 30px;
    line-height: 36px;
    color: #545454;
    padding: 28px 36px;
    border-bottom: 1px solid #ededed;
    text-align: justify;
  }
  .tips-txt{
    line-height: 36px;
  }
  .closeBtn{
    position: fixed;
    bottom: 40px;
    right: 36px;
    left: 36px;
    margin: auto;
  }
</style>



