<template>
  <div id="organizationNext">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="strong-line"></div>
    <h3 class="tip-box"></h3>
    <div class="e-field">
      <mt-field label="1" placeholder="" v-model="username" type="number"></mt-field>
      <mt-field label="2" placeholder="" v-model="username" type="number"></mt-field>
      <mt-field label="" placeholder="" v-model="linkman"></mt-field>
      <div class="tips"></div>
    </div>
    <div class="e-field">
      <h3 class="area-title"></h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="--/-"></textarea>
      </div>
    </div>
    <div class="btn-box">
      <router-link to="/preview/organ"><div class="submit-btn"></div></router-link>
      <router-link to="/"><div class="submit-btn"></div></router-link>
    </div>
  </div>
</template>


<script>
  import appHeader from "@/components/public/appHeader"

  export default {
    name: "next",
    data(){
      return{
        showTitle:"",
        username:"",
        phone:"",
        linkman:"",
        linkphone:"",
        birthday:'',
      }
    },
    components:{
      appHeader,
    },
    created:function(){
    },
    methods:{
      backUp:function () {
        this.$refs.child.backUp();
      },
    }
  }
</script>

<style scoped lang="less">
.tips{
  width:750px;
  font-size:24px;
  font-weight:400;
  color:rgba(177,177,177,1);
  line-height:36px;
  padding-top: 30px;
  padding-left: 36px;
  padding-right: 36px;
  padding-bottom: 0;
}

</style>






