<template>
    <div id="enter">
      <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
      <div class="strong-line"></div>
      <div class="e-content">
        <h3 class="e-title"><img src="../../assets/img/enter/tianxie.png" alt=""></h3>
        <p class="e-text">
          1. 
          
        </p>
        <p class="e-text">
          2. 
          
        </p>
      </div>
      <div class="e-content">
        <h3 class="e-title"><img src="../../assets/img/enter/weibiaoti.png" alt=""></h3>
        <p class="e-text">
          1. 3~5
          
        </p>
        <p class="e-text">
          2. 
        </p>
        <p class="e-text">3. </p>
      </div>
      <div class="e-content">
        <h3 class="e-title"><img src="../../assets/img/enter/chenggong.png" alt=""></h3>
      </div>
      <button class="btn" @click="goTo('/enterSelect')"></button>
    </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader"
    export default {
      name: "index",
      data() {
        return {
          showTitle:""
        }
      },
      components: {
        appHeader
      },
      methods:{
        backUp(){
          this.$refs.child.backUp();
        }
      }
    }
</script>

<style scoped lang="less">
  .e-content{
    border-bottom: 1px solid #ededed;
    padding: 0 36px 10px;
    .e-title{
      font-size: 36px;
      font-weight: 400;
      padding: 28px 0;
      img{
        width: 36px;
        height: 36px;
        vertical-align: text-bottom;
        margin-right: 28px;
      }
    }
    .e-text{
      font-size:30px;
      font-weight:400;
      color:rgba(84,84,84,1);
      line-height:45px;
      margin-bottom: 10px;
    }
  }
  .btn{
    position: fixed;
    bottom: 60px;
    right: 0;
    left: 0;
    margin: auto;
  }
</style>



