<template>
  <div id="organization">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="strong-line"></div>
    <h3 class="tip-box"></h3>
    <div class="e-field">
      <mt-field label="" placeholder="" v-model="username"></mt-field>
      <div class="field-item upState">
        <div class="field-title"></div>
        <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
                @upload="getImgUrl" class="upload"></upload>
      </div>
      <div class="field-item">
        <label for="type"></label>
        <select name="" id="type">
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </select>
      </div>
      <div class="field-item">
        <label for="city"></label>
        <button @click="showArea" id="city" class="show-btn">{{btnTxt}}</button>
        <div class="pop-box" v-show="popupVisible">
          <button @click="hideArea" class="close-btn"></button>
          <eap-area v-on:childEvent="changeCity"></eap-area>
        </div>
      </div>
      <mt-field label="" placeholder="" v-model="linkman"></mt-field>
      <mt-field label="" placeholder="" type="tel" v-model="linkphone"></mt-field>
      <mt-field label="()" placeholder="" v-model="linkman"></mt-field>
    </div>
    <div class="strong-line2"></div>
    <div class="e-field">
      <h3 class="area-title"></h3>
      <div class="area-box">
        <textarea class="area-input" placeholder="2000"></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="area-title"><b></b></h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="--/-"></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="area-title"><b></b></h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="200"></textarea>
      </div>
    </div>
    <div class="btn-box">
      <router-link to="/organization/next"><div class="submit-btn"></div></router-link>
    </div>
  </div>
</template>


<script>
  import appHeader from "@/components/public/appHeader"
  import upload from "@/components/public/upload";
  import eapArea from "@/components/public/area";

  export default {
    name: "organization",
    data(){
      return{
        showTitle:"",
        year:[],
        username:"",
        phone:"",
        linkman:"",
        linkphone:"",
        birthday:'',
        selectData: {
          defaultIndex: 0, //
          selectStatus: false, // selectStatus/
        },
        popupVisible:false,
        btnTxt:"",
        imgUrl:""
      }
    },
    components:{
      appHeader,
      eapArea,
      // oSelect,
      //
      upload
    },
    created:function(){
    },
    methods:{
      backUp:function () {
        this.$refs.child.backUp();
      },
      showArea(){
        this.popupVisible=true;
      },
      hideArea(){
        this.popupVisible=false;
      },
      changeCity(data){
        console.log(data);
        this.btnTxt=data;
      },
      //emit
      getImgUrl(data) {
        //data  
        console.log(data)
      }
    }
  }
</script>

<style scoped lang="less">
</style>





