  <div id="orderDetail">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="strong-line"></div>
    <div class="eap-data">
      <div class="eap-pic">
        <img src="" alt="">
      </div>
      <div class="eap-state">
        <p class="eap-name"></p>
      </div>
    </div>
    <div class="state-box">
      <div class="strong-line2"></div>
      <div class="order-data">
        <h3 class="collapse-title"></h3>
        <button class="collapse-btn" @click="collapseToggle" :class="showCollapse?'active':''"></button>
      </div>
      <div class="collapse-box" :class="showCollapse?'':'hide'">
        <div class="order-data">
          <div class="data-box bb1">
            <p>12343443523</p>
            <p>2019-03-20 14:00</p>
            <p></p>
            <p></p>
          </div>
        </div>
        <div class="strong-line2"></div>
        <div class="person-data">
          <h3></h3>
          <div class="data-box">
            <p></p>
            <p></p>
            <p>30</p>
            <!--<p>{{dateFormat(orderTime)}}</p>-->
            <p></p>
            <p></p>
            <p>13422818291</p>
          </div>
          <div class="strong-line2"></div>
          <div class="order-data">
            <h3></h3>
            <div class="data-box bb1">
              <p>2019-03-2114:00</p>
              <p>2019-03-2114:00-14:50</p>
              <div v-if="flag=='cancel'">
                <p>2019-03-2114:00</p>
                <p></p>
              </div>
              <div class="tips-txt" v-if="flag=='consult'">
                90
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="btn-box" v-if="flag=='accept'">
        <button @click="showCover=true" class="noway"></button>
        <router-link to="/order" class="ok"></router-link>
        <div class="cover-box" v-show="showCover">
            <div class="out-box">
              <b class="xbtn" @click="showCover=false">&times;</b>
              <div class="operate-box">
                <h3 class="tip-box"></h3>
                <div class="area-box height300">
                  <textarea class="area-input" placeholder=""></textarea>
                </div>
                <div class="btn-box">
                  <button class="closeBtn" @click="goTo('/order')"></button>
                </div>
              </div>
            </div>
          </div>
      </div>
    </div>
    <div class="state-box" v-if="flag=='complete'">
      <div class="strong-line2"></div>
      <div class="person-data">
        <h3></h3>
        <div class="data-box">
          <p>50</p>
          <p></p>
          <p></p>
          <!--<p>{{dateFormat(orderTime)}}</p>-->
          <p></p>
          <p></p>
        </div>
      </div>
      <div class="strong-line2"></div>
      <div class="person-data">
        <h3 class="bt"></h3>
        <div class="data-box">
          <p></p>
          <p>
            ?(
            
          </p>
          <p>
            18151815181518151815
          </p>
        </div>
      </div>
      <div class="person-data">
        <h3 class="bt"></h3>
        <div class="data-box">
          <p> </p>
          <p>
            18151815181518151815
          </p>
          <p> </p>
          <p>
            18151815181518151815
          </p>
        </div>
      </div>
      <div class="person-data">
        <h3 class="bt"></h3>
        <div class="data-box">
          <p></p>
          <p>
            18151815181518151815
          </p>
          <p></p>
          <p>18151815181518151815</p>
          <p>
            
            
          </p>
          <p>18151815181518151815</p>
        </div>
      </div>

    </div>
    <div class="state-box" v-if="flag=='feedback'">
      <div class="strong-line2"></div>
      <div class="field-item">
        <label></label>
        <label class="radio-label">
          <input type="radio" name="break" value="/order" id="male " v-model="isBreak">
          <span></span>
        </label>
        <label class="radio-label" for="female">
          <input type="radio" name="break" value="/record" id="female" v-model="isBreak">
          <span></span>
        </label>
      </div>
      <button @click="submitBtn" class="closeBtn"></button>

    </div>
  </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader.vue";

  export default {
    name: "orderDetail",
    data() {
      return {
        showTitle: "",
        pickerValue:"",
        flag: this.$route.params.flag,
        isBreak:false,
        showCover:false,
        showCollapse:true
      }
    },
    components: {
      appHeader
    },
    mounted(){
      this.flag=='accept'&&(this.showTitle='');
      this.flag=='consult'&&(this.showTitle='');
      this.flag=='feedback'&&(this.showTitle='');
    },
    methods:{
      backUp () {
        this.$refs.child.backUp();
      },
      //yyyy-MM-dd hh:mm:ss 
      dateFormat:function(time) {
        var date=new Date(time);
        var year=date.getFullYear();
        /* 00
         * 100   09:11:05
         * */
        var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
        var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
        var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
        var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
        var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
        // 
        return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
      },
      submitBtn(){
          this.$router.push(this.isBreak)
      },
      collapseToggle(){
        let a=this.showCollapse;
        this.showCollapse=!a;
      }
    }
  }
</script>

<style scoped lang="less">
  .eap-data{
    padding: 36px 36px 28px;
    overflow: auto;
    border-bottom: 1px solid #ededed;
    h3{
      font-weight: 400;
      font-size: 30px;
      line-height: 84px;
    }
    .eap-pic{
      width:80px;
      height:80px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      border-radius:10px;
      margin-right: 40px;
      float: left;
    }
    .eap-state{
      float: left;
      .eap-name{
        font-size: 36px;
        line-height: 80px;
      }
    }
  }
  .person-data,.order-data{
    h3{
      font-weight: 400;
      font-size: 30px;
      line-height: 84px;
      padding: 0 36px;
      border-bottom: 1px solid #ededed;
      &.bt{
        border-top: 1px solid #ededed;
      }
    }
    .data-box{
      position: relative;
      padding: 14px 36px 28px;
      p{
        line-height: 50px;
      }
      .time-btn{
        font-size:24px;
        color:#4facfe;
        position: absolute;
        top: 46%;
        right: 36px;
      }
    }
  }
  .order-data{
    position: relative;
    .data-box{
      padding-bottom: 14px;
      &.bb1{
        border-bottom: 1px solid #ededed;
      }
    }
    .book{
      color:#4facfe;
    }
  }
  .tips-txt{
    margin: 0;
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 36px;
  }

  .btn-box{
    margin: 0 auto;
    text-align: center;
    display: flex;
    .ok,.noway{
      flex: 1;
      line-height: 100px;
      margin: 0;
      padding: 0;
      height:100px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      font-size:36px;
      color:rgba(255,255,255,1);
    }
    .noway{
      background: #EDEDED;
      color: #545454;
    }
  }
  .field-item label.radio-label{
    width: 160px;
  }
  .closeBtn{
    margin: 28px 36px;
  }
  .cover-box{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.6);
    left: 0;
    top: 0;
    z-index: 999;
    .out-box{
      width: 600px;
      height: 600px;
      border-radius: 20px;
      background: #fff;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      .xbtn{
        font-size: 40px;
        position: absolute;
        right: 16px;
        top: 16px;

      }
      .tip-box{
        padding-top: 28px;
        text-align: left;
      }
      .area-box {
        width: 100%;
        padding: 0 36px;
        .area-input {
          width: 100%;
        }
      }
    }
  }
.collapse-box{
  max-height: 1200px;
  opacity: 1;
  visibility: visible;
  transition: max-height .2s;
  &.hide{
    max-height: 0;
    opacity: 0;
    visibility: hidden;
  }
}
  h3.collapse-title{
    font-size: 36px;
    font-weight: 400;
  }
.collapse-btn{
  position: absolute;
  right: 18px;
  top: 0;
  bottom: 0;
  margin: auto;
  background: url("../../assets/img/xia.png") no-repeat center;
  width: 72px;
  height: 36px;
  background-size: 36px 18px;
  transform: rotate(180deg);
  transition: transform .3s;
  &.active{
    transform: rotate(0deg);
  }
}
</style>



