<template>
  <div id="step1" class="step-container">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <ul class="step-list">
      <li :class="item.index==activeClass[index]?'active':''" v-for="(item,index) in stepList">
        <img :src="getUrl(item.index)" alt="" v-if="activeClass[index]==item.index">
        <img :src="getUrl('2'+item.index)" alt="" v-if="activeClass[index]!=item.index">
        <span>{{item.txt}}</span>
      </li>
    </ul>
    <div class="field-item upState">
      <div class="field-title"></div>
      <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
              @upload="getImgUrl" class="upload"></upload>
      <p class="tips"></p>
    </div>
    <div class="field-item upState id-card">
      <div class="field-title"></div>
      <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
              @upload="getImgUrl" class="upload"></upload>
      <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
              @upload="getImgUrl" class="upload"></upload>
      <p class="tips"></p>
    </div>
    <mt-field label="" placeholder="" v-model="username"></mt-field>
    <mt-field label="" placeholder="" v-model="idCard" type="number"></mt-field>

    <div class="field-item">
      <label for="sex"></label>
      <select name="" id="sex">
        <option></option>
        <option></option>
        <option></option>
      </select>
    </div>
    <div class="field-item">
      <label for="city"></label>
      <button @click="showArea" id="city" class="show-btn">{{btnTxt}}</button>
      <div class="pop-box" v-show="popupVisible">
        <button @click="hideArea" class="close-btn"></button>
        <eap-area v-on:childEvent="changeCity"></eap-area>
      </div>
    </div>
    <div class="field-item">
      <label for="edu"></label>
      <select name="" id="edu">
        <option></option>
        <option></option>
        <option></option>
        <option></option>
        <option></option>
        <option></option>
      </select>
    </div>
    <div class="field-item">
      <label for="marry"></label>
      <select name="" id="marry">
        <option></option>
        <option></option>
        <option></option>
        <option></option>
      </select>
    </div>
    <div class="field-item">
      <label for="children"></label>
      <select name="" id="children">
        <option></option>
        <option></option>
        <option>1</option>
        <option>2</option>
        <option>3</option>
        <option>4</option>
        <option>5</option>
      </select>
    </div>
    <mt-field label="" placeholder="" v-model="phone" type="number"></mt-field>
    <mt-field label="" placeholder="" v-model="year" type="number"></mt-field>
    <div class="field-item">
      <label for="type"></label>
      <select name="" id="type">
        <option></option>
        <option></option>
        <option></option>
        <option></option>
        <option></option>
      </select>
    </div>
    <div class="field-item">
      <label for="theme"></label>
      <select name="" id="theme">
        <option></option>
        <option></option>
        <option></option>
        <option></option>
        <option></option>
      </select>
    </div>
    <div class="field-item">
      <label for="crowd"></label>
      <select name="" id="crowd">
        <option></option>
        <option></option>
        <option></option>
        <option></option>
        <option></option>
      </select>
    </div>
    <mt-field label="" placeholder="()" v-model="language"></mt-field>
    <div class="e-field">
      <h3 class="area-title"></h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="--/-"></textarea>
      </div>
    </div>


    <div class="btn-box">
      <router-link to="/eap/step2"><div class="submit-btn" @click="goStep(2)"></div></router-link>
    </div>
  </div>
</template>

<script>
  import eapArea from "@/components/public/area";
  import upload from "@/components/public/upload";
  import appHeader from '@/components/public/appHeader';
    export default {
      name: "step1",
      data(){
        return{
          imgUrl:"",
          activeClass:[],
          imgBaseUrl:"../../assets/img/seminar/",
          stepList:this.$store.state.stepList,
          showTitle:'',
          username:'',
          language:"",
          idCard:'',
          selectData: {
            defaultIndex: 0, //
            selectStatus: false, // selectStatus/
          },
          popupVisible:false,
          btnTxt:"",
          phone:'',
          year:''
        }
      },
      created(){
        this.$store.commit("stepGo",1);
      },
      mounted(){
        this.activeClass=this.$store.state.stepClass;
      },
      components:{
        upload,
        appHeader,
        eapArea
      },
      methods:{
        //emit
        getImgUrl(data) {
          //data  
          console.log(data)
        },
        //
        getUrl(icon){
          return require("@/assets/img/seminar/"+icon+".png");
        },
        backUp(){
          this.$refs.child.backUp();
        },
        showArea(){
          this.popupVisible=true;
        },
        hideArea(){
          this.popupVisible=false;
        },
        changeCity(data){
          console.log(data);
          this.btnTxt=data;
        },
      }
    }
</script>

<style lang="less">
  #step1 {
    .id-card .avatar{
      &:last-of-type{
        margin-left: 0;
      }
    }
  }

  .id-card .tips{
    width: 172px;
  }
</style>



