<template>
    <div id="record" class="record">
      <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
      <div class="strong-line"></div>
      <h3 class="tip-box"></h3>
      <mt-field label="" placeholder="" v-model="time" type="number" :class="'timeAfter'"></mt-field>
      <div class="field-item">
        <label></label>
        <label class="radio-label">
          <input type="radio" name="withWork" value="true" id="male " v-model="withWork">
          <span></span>
        </label>
        <label class="radio-label" for="female">
          <input type="radio" name="withWork" value="false" id="female" v-model="withWork">
          <span></span>
        </label>
      </div>
      <div class="chk-box">
        <label  v-for="(item,index) in questionList" :key="index" @click="showR=false">
          <input type="radio" :value="item" name="question" v-model="activeValue">
          {{item}}
        </label>
      </div>
      <div :class="activeValue==''?'show':'hide'">
        <div class="field-item">
          <label></label>
          <select name="questionType1" v-model="questionType" v-on:change="showR=true">
            <option value=""></option>
            <option value="1"></option>
            <option value="2"></option>
            <option value="3"></option>
            <option value="4"></option>
            <option value="5"></option>
            <option value="6"></option>
            <option value="7"></option>
            <option value="8"></option>
          </select>
        </div>
        <div class="chk-box chk-box2">
          <div :class="questionType=='1'?'show':'hide'">
            <label v-for="(item,index) in queList[1]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
            <label>
              <input type="radio" :value="other" name="que1">
              {{queList[1].length+1+"."}}
              <input type="text" class="otherInput" v-model="other">
            </label>
          </div>
          <div :class="questionType=='2'?'show':'hide'">
            <label v-for="(item,index) in queList[2]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
          </div>
          <div :class="questionType=='3'?'show':'hide'">
            <label v-for="(item,index) in queList[3]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
          </div>
          <div :class="questionType=='4'?'show':'hide'">
            <label v-for="(item,index) in queList[4]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
            <label>
              <input type="radio" :value="other" name="que1">
              {{queList[4].length+1+"."}}
              <input type="text" class="otherInput" v-model="other">
            </label>
          </div>
          <div :class="questionType=='5'?'show':'hide'">
            <label v-for="(item,index) in queList[5]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
          </div>
          <div :class="questionType=='6'?'show':'hide'">
            <label v-for="(item,index) in queList[6]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
          </div>
          <div :class="questionType=='7'?'show':'hide'">
            <label v-for="(item,index) in queList[7]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
            <label>
              <input type="radio" :value="other" name="que1">
              {{queList[7].length+1+"."}}
              <input type="text" class="otherInput" v-model="other">
            </label>
          </div>
          <div :class="questionType=='8'?'show':'hide'">
            <label v-for="(item,index) in queList[8]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
            <label>
              <input type="radio" :value="other" name="que1">
              {{queList[8].length+1+"."}}
              <input type="text" class="otherInput" v-model="other">
            </label>
          </div>
        </div>
      </div>
      <div :class="activeValue==''?'show':'hide'">
        <div class="field-item">
          <label></label>
          <select name="questionType2" v-model="questionType" v-on:change="showR=true">
            <option value=""></option>
            <option value="1"></option>
            <option value="2"></option>
            <option value="3"></option>
            <option value="4"></option>
            <option value="5"></option>
            <option value="6"></option>
          </select>
        </div>
      </div>
      <div :class="activeValue==''?'show':'hide'">
        <mt-field label="" placeholder="DSM-ICD-10CCMD-3" v-model="serious"></mt-field>
        <div class="chk-box risk-box bt0">
          <label class="risk">
            <input type="checkbox" value="" name="risk" v-model="risk">
            
          </label>
        </div>
      </div>
      <div :class="activeValue==''?'show':'hide'">
        <div class="chk-box risk-box chk-box2">
          <label class="risk" v-for="(item,index) in crisisList">
            <input type="checkbox" :value="item" name="crisis" v-model="crisis[index]">
            {{item}}
          </label>
          <label>
            <input type="checkbox" :value="other" name="crisis1">
            {{"6."}}
            <input type="text" class="otherInput" v-model="other" value="">
          </label>
        </div>
      </div>
      <div :class="activeValue==''?'show':'hide'">
        <div class="chk-box risk-box chk-box2">
          <label>
            <input type="radio" :value="commend[0]" name="commend2">
            {{"1."}}
            <input type="text" class="otherInput" v-model="commend[0]" value="">
          </label>
          <label>
            <input type="radio" :value="commend[1]" name="commend2">
            {{"2.&#12288;"}}
            <input type="text" class="otherInput" v-model="commend[1]" value="">
          </label>
        </div>
        <div class="chk-box risk-box">
          <label class="risk">
            <input type="checkbox" value="" name="risk" v-model="risk">
            
          </label>
        </div>
      </div>
      <div :class="activeValue==''?'show':'hide'">
        <div class="chk-box chk-box2">
          <label v-for="(item,index) in interveneList">
            <input type="radio" :value="item" name="intervene" v-model="intervene">
            {{item}}
          </label>
        </div>
      </div>

      <div v-show="showR" class="chk-box risk-box">
        <label class="risk">
          <input type="checkbox" value="" name="risk" v-model="risk">
          
        </label>
      </div>
      <div class="btnBox">
        <button class="closeBtn" @click="nextGo"></button>
      </div>

    </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
    export default {
      name: "record",
      data(){
        return{
          showTitle:'',
          time:'',
          withWork:"",
          questionList:["","","","","",""],
          queList:[
            [],
            ["1.","2.","3.","4.","5.","6.","7.","8.","9."],
            ["1.","2.","3.","4.","5.","6.","7.","8.","9."],
            ["1.","2.","3.","4.","5.","6."],
            ["1.","2.","3.","4.","5.","6."],
            ["1.","2.","3.","4.","5."],
            ["1.","2."],
            ["1.","2.","3.","4."],
            ["1.","2.","3.","4."]
          ],
          crisis:[],
          crisisList:["1.","2.","3.","4.","5."],
          activeValue:"",
          questionType:'',
          queValue:"",
          risk:"",
          other:"",
          showRisk:false,
          commend:[],
          interveneList:["A ","B ","C "],
          intervene:"",
          serious:""
        }
      },
      created(){
      },
      watch:{
        activeValue(val){
          this.questionType="";
        },
        questionType(val){
          this.queValue="";
        },
        // intervene(val){
        //   console.log(val)
        // }
      },
      computed:{
        showR:{
          get:function(){
            console.log(this.showRisk);
            return this.showRisk
          },
          set:function(val){
            this.showRisk=val;
          }
        }
      },
      components:{
        appHeader
      },
      methods:{
        backUp(){
          this.$refs.child.backUp();
        },
        getOther(obj){
          let arr=obj[obj.length-1];
          return arr;
        },
        nextGo(){
          if(this.withWork){
            this.$router.push("/record2")
          }else {
            this.$router.push("/record3")
          }
        }
      }
    }
</script>

<style scoped lang="less">
  .record{
    padding-bottom: 150px;
    overflow: auto;
  }
  .show{
    display: block;
    position: relative;
  }
  .hide{
    display: none;
  }
  .tip-box{
    border-bottom: 1px solid #ededed;
  }
  .timeAfter{
    position: relative;
    &:after{
      content: "";
      display: block;
      position: absolute;
      right: 36px;
      border-bottom: 0;
      font-size: 30px;
      width: 80px;
      height: 84px;
      line-height: 84px;
    }
  }

  .chk-box{
    padding-left: 50px;
    padding-right: 50px;
    border-bottom: 1px solid #ededed;
    &.chk-box2{
      padding: 20px 50px;
      border: 0;
      overflow: auto;
      label{
        width: 100%;
        float: left;
        &:nth-child(2n){
          width:100%;
        }
      }
    }
    label{
      position: relative;
      display: inline-block;
      font-size: 30px;
      width: 55%;
      line-height: 80px;
      &:nth-child(2n){
        width: 45%;
      }
    }
    input[type='radio'],input[type='checkbox'] {
      width: 38px;
      height: 38px;
      overflow: visible;
      background: url("../../assets/img/consult/radioBg.png") no-repeat;
      background-size: 100%;
      vertical-align: sub;
      border: 0;
      outline: 0;
      -webkit-appearance: none;
    }

    input:checked{
      position: relative;
      background: #fff;
    }

    input:checked::after {
      position: absolute;
      background: url("../../assets/img/consult/radioicon.png") no-repeat;
      background-size: 100%;
      content: "";
      width: 38px;
      height: 38px;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
  }
  .risk-box{
    width: 100%;
    border-top: 1px solid #ededed;
    &.bt0{
      border-top: 0;
    }
  }
  .closeBtn{
  }
  .btnBox{
    width: 100%;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    padding-bottom: 28px;
  }
  .otherInput{
    position: absolute;
    width: 240px;
    height: 46px;
    top: 14px;
    outline: 0;
    border: 0;
    border-bottom: 2px solid #545454;
    font-size: 30px;
    padding-left: 10px;
  }
  .heightAuto{
    height: auto;
  }
</style>



