const cityList=[];
const districtList=[];

function getFirstPrinvince(){
  for(var item of city){
    privinceList.push(item.name);
  }
}

function getFirstCityList(){
  for(var cityitem of city[0].sub){
    cityList.push(cityitem.name);
  }
}
function getFirstDistrictList(){
  for(var disitem of city[0].sub[0].sub){
    districtList.push(disitem.name);
  }
}
getFirstPrinvince();
