<template>
  <div id="step3" class="step-container">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <ul class="step-list">
      <li :class="item.index==activeClass[index]?'active':''" v-for="(item,index) in stepList">
        <img :src="getUrl(item.index)" alt="" v-if="activeClass[index]==item.index">
        <img :src="getUrl('2'+item.index)" alt="" v-if="activeClass[index]!=item.index">
        <span>{{item.txt}}</span>
      </li>
    </ul>

    <div class="field-item chose-field">
      <label></label>
      <div class="btn-box">
        <button class="choseBtn" :class="eapType==0?'active':''" @click="eapType=0"></button>
        <button class="choseBtn" :class="eapType==1?'active':''" @click="eapType=1"></button>
      </div>
    </div>
    <div class="field-item chose-field">
      <label><b></b></label>
      <div class="btn-box">
        <button class="choseBtn" :class="acceptType[0]==true?'active':''" @click="acceptBtn(0,'type')"></button>
        <button class="choseBtn" :class="acceptType[1]==true?'active':''" @click="acceptBtn(1,'type')"></button>
      </div>
    </div>
    <div class="field-item chose-field">
      <label><b></b></label>
      <div class="btn-box three">
        <button class="choseBtn" :class="acceptWay[0]==true?'active':''" @click="acceptBtn(0,'way')"></button>
        <button class="choseBtn" :class="acceptWay[1]==true?'active':''" @click="acceptBtn(1,'way')"></button>
        <button class="choseBtn" :class="acceptWay[2]==true?'active':''" @click="acceptBtn(2,'way')"></button>
      </div>
    </div>
    <mt-field label="" placeholder="" v-model="phone" type="number"></mt-field>
    <div class="field-item">
      <label for="tool"></label>
      <select name="" id="tool">
        <option></option>
        <option></option>
        <option>QQ</option>
      </select>
    </div>
    <mt-field label="" placeholder="" v-model="num" type="number"></mt-field>
    <div class="e-field">
      <h3 class="area-title"></h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder=" --/-"></textarea>
      </div>
    </div>
    <div class="strong-line" style="padding-bottom: 20px"></div>

    <h3 class="tip-box"></h3>
    <div class="line"></div>
    <mt-field label="" placeholder="" v-model="num"></mt-field>
    <mt-field label="" placeholder="" v-model="num"></mt-field>
    <div class="tips-txt">1. 2. </div>

    <div class="btn-box">
      <router-link to="/preview/eap"><div class="submit-btn"></div></router-link>
      <router-link to="/eap/step4"><div class="submit-btn" @click="goStep(4)"></div></router-link>
    </div>
  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "step2",
    data(){
      return{
        imgUrl:"",
        activeClass:[],
        imgBaseUrl:"../../assets/img/seminar/",
        stepList:this.$store.state.stepList,
        showTitle:'',
        eapType:0,
        acceptType:[true,false],
        acceptWay:[true,false,false],
        phone:'',
        num:""
      }
    },
    created(){
    },
    mounted(){
      this.activeClass=this.$store.state.stepClass;
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$store.commit("stepGo",2);
        this.$refs.child.backUp();
      },
      //emit
      getImgUrl(data) {
        //data  
        console.log(data)
      },
      //
      getUrl(icon){
        return require("@/assets/img/seminar/"+icon+".png");
      },
      acceptBtn(id, flag) {
        console.log(id,flag)
        if (flag == 'type') {
          this.$set(this.acceptType,id,!this.acceptType[id]);
        } else if (flag == 'way') {
          this.$set(this.acceptWay,id,!this.acceptWay[id]);
          console.log(this.acceptWay)
        }
      }
    }
  }
</script>

<style scoped lang="less">
  .field-item.chose-field{
    height: auto;
    padding-bottom: 14px;
    label{
      display: block;
      float: none;
      width: auto;
      b{
        font-weight: normal;
        color: #b1b1b1;
      }
    }
  }
  .btn-box{
    .choseBtn{
      width:120px;
      height:40px;
      background:#fff;
      border: 2px solid #bfbfbf;
      color: #B1B1B1;
      border-radius:6px;
      text-align: center;
      line-height: 40px;
      margin-right: 260px;
      &.active{
        color: #fff;
        background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
        border: 0;
      }
      &:last-child{
        margin-right: 0;
      }
    }
    &.three{
      .choseBtn{
        margin-right: 66px;
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
</style>



